:root {
  --font_color: #fff;
  --bg_color: #222;
}

@keyframes colorful {
  0% {
    color: #fff;
    filter: blur(1px) hue-rotate(0deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff, 0 0 25px #00b3ff, 0 0 30px #00b3ff;
  }

  100% {
    color: #fff;
    filter: blur(1px) hue-rotate(360deg);
    text-shadow: 0 0 5px #00b3ff, 0 0 10px #00b3ff, 0 0 15px #00b3ff,
      0 0 20px #00b3ff;
  }
}

/* layout */
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 1.5rem;
  background-color: var(--bg_color);
  color: var(--font_color);
  box-sizing: border-box;
  position: relative;
}

li {
  list-style: none;
}

a {
  color: var(--font_color);
  text-decoration: none;
}

.inner {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}

.clearfix:after {
  content: "";
  display: block;
  clear: both;
}

/* header */
#header {
  width: 100%;
  height: 90px;
  border-bottom: 1px solid rgba(100, 100, 100, 0.5);
  position: relative;
}

#header span {
  position: absolute;
  top: calc(50% - 22.5px);
  left: 75px;
  animation: colorful 4s linear infinite;
}

#header span a {
  font-size: 2rem;
}

/* header ul */
#header .menu {
  position: absolute;
  right: 75px;
  top: calc(50% - 22.5px);
}

#header .menu li {
  float: left;
  width: 125px;
  padding: 8px;
  text-align: center;
  font-weight: bold;
  position: relative;
  overflow: hidden;
}

#header .menu li:before {
  content: "";
  display: none;
  background: pink;
  width: 40px;
  height: 350%;
  position: absolute;
  left: calc(50% - 20px);
  top: calc(-50% - 40px);
  animation: spin 1.5s linear infinite;
}

#header .menu li:hover:before {
  display: block;
  z-index: -1;
}

#header .menu li:after {
  content: "";
  display: none;
  position: absolute;
  background: #222;
  inset: 3px;
}

#header .menu li:hover:after {
  display: block;
  z-index: -1;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#header svg {
  display: none;
}


/* Section 부분 */
/* ./components/menu/Home */
#Home {
  width: 100%;
  height: 85vh;
}

#Home .inner {
  position: relative;
  max-width: 1280px;
  height: 100%;
}

#Home .inner>div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  top: calc(50% - 25vh);
}

#Home div .sparkling {
  position: relative;
  height: 80%;
  width: 50%;
  left: 10%;
  top: 50px;
  /* display: flex;
  justify-content: flex-start;
  align-items: center; */
}

#Home .home_text {
  width: 50%;
  height: 100%;
}

#Home .home_text h2 {
  font-size: 5rem;
  display: block;
}

#Home .home_text p {
  margin-top: 12px;
  line-height: 77px;
}

#Home .home_text p span {
  font-size: 2.5rem;
  color: #222;
  background: #fff;
  padding: 3px 50px;
  font-weight: bold;
}

/* ./components/menu/Resume */
#resume {
  width: 100%;
  height: 85vh;
  display: flex;
  align-items: center;
}

#resume * {
  color: #222;
}

#resume .inner {
  width: 90%;
  height: 90%;
  margin: 0 auto;
  position: relative;
}

/* 모서리 접힌 부분 구현 */
#resume .inner:after {
  content: '';
  width: 0;
  height: 0;
  right: 0;
  position: absolute;
  border-bottom: 75px solid #fbfbfb;
  border-top: 75px solid var(--bg_color);
  border-left: 75px solid #fbfbfb;
  border-right: 75px solid var(--bg_color);
  box-shadow: -1px 2px 5px rgba(0, 0, 0, 0.3),
    -1px 2px 10px rgba(0, 0, 0, 0.5);
}

#resume .inner .resume_wrap {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

#resume .inner .resume_wrap .main {
  width: 97.5%;
  height: 95%;
  border: 5px solid black;
  border-style: dashed;
  position: relative;
  display: flex;
  justify-content: center;
}

#resume .inner .resume_wrap .main>div {
  padding-top: 25px;
}

#resume .main hr {
  border: 2px solid;
  margin: 4px 0 1rem 0;
}

#resume .main p,
#resume .main li {
  font-size: 22px;
  font-weight: normal;
}

/* ch */
#resume .main .L_main {
  width: 35%;
  height: 100%;
}

#resume .main .L_main figure {
  width: 100%;
  height: 50%;
  padding-left: 100px;
}

#resume .main .L_main figure img {
  width: 300px;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

#resume .main .L_main div {
  padding-left: 50px;
  margin-top: 1.5rem;
  line-height: 50px;
}

/* age, e-mail */
#resume .main .M_main {
  width: 15%;
  height: 100%;
}

/* education, certificate, skills */
#resume .main .R_main {
  width: 40%;
  height: 100%;
}

#resume .main .R_main div {
  width: 70%;
  margin-left: 50px;
  margin-bottom: 50px;
  position: relative;
}

#resume .main .R_main table,
#resume .main .R_main th,
#resume .main .R_main td {
  border: 1px solid #222;
  font-size: 1rem;
  letter-spacing: -1px;
}

#resume .main .R_main table {
  width: 100%;
  border-collapse: collapse
}

#resume .main .R_main th {
  padding: 4px 0;
}

#resume .main .R_main td {
  padding: 4px;
}

/* resume skills */
#resume .main .R_main div:last-child h3 {
  display: inline-block;
  margin-right: 2rem;
}

#resume .main .R_main div:last-child button {
  position: relative;
  width: 125px;
  height: 37px;
  background: transparent;
  transition: 1s;
}

#resume .main .R_main div:last-child button:hover {
  background: #000;
}

/* 자세히보기 버튼 툴팁 */
#resume .main .R_main div:last-child button::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  border-bottom: 5px solid transparent;
  border-top: 5px solid #000;
  border-left: 5px solid #000;
  border-right: 5px solid transparent;
}

#resume .main .R_main div:last-child button:after {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  border-bottom: 5px solid #000;
  border-top: 5px solid transparent;
  border-left: 5px solid transparent;
  border-right: 5px solid #000;
}

#resume .main .R_main div:last-child button a {
  font-size: 1rem;
  width: 100%;
  height: 100%;
  line-height: 37px;
  font-weight: bold;
  transition: 1s;
}

#resume .main .R_main div:last-child button:hover a {
  color: #fff;
}

#resume .main .R_main div:last-child .skill_list {
  margin-top: 1rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

#resume .main .R_main div:last-child .skill_list li {
  list-style: none;
  width: 50px;
  height: 50px;
  margin-right: 1rem;
  margin-bottom: 1rem;
}

/* ./components/menu/projects (swiper)*/
#projects {
  width: 100%;
  height: 85vh;
  position: relative;
  overflow: scroll;
}

#projects .inner {
  max-width: 1400px;
}

#projects .inner>ul {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}

#projects .inner>ul li {
  margin-right: 2rem;
  padding: 4px 16px;
  border-radius: 50px;
  background: #fff;
  color: #000;
  letter-spacing: -1px;
  cursor: pointer;
}
#projects .inner>ul li.on{
  color:red;
}
#projects .inner .title {
  margin-bottom: 25px;
  padding: 0 20px 8px 8px;
  border-bottom: 3px solid #fff;
}


/* swiper 공통 부분 */
#projects .swiper .swiper-button-prev,
#projects .swiper .swiper-button-next {
  position: absolute;
  width: 30px;
  height: 30px;
  background: #fff;
  left: calc(50% - 50px);
  bottom: 0;
  border-radius: 5px;
  cursor: pointer;
  line-height: 20px;
  text-align: center;
}

#projects .swiper .swiper-button-next {
  left: calc(50% + 20px);
}
#projects .swiper .swiper-button-prev:after{
  content: '<';
  color:#000;
  font-size: 2.5rem;
}
#projects .swiper .swiper-button-next:after {
  content: '>';
  color: #000;
  font-size: 2.5rem;
}
#projects .swiper .swiper-button-prev.swiper-button-disabled:after,
#projects .swiper .swiper-button-next.swiper-button-disabled:after{
  color:rgba(0, 0, 0, 0.3);
}

#projects .swiper-slide {
  margin-bottom: 100px;
}

#projects .swiper-slide .item_img {
  max-height: 360px;
  position: relative;
  transition: all 1s;
}

#projects .swiper-slide .item_img img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

#projects .swiper-slide .item_img button {
  position: absolute;
  left: calc(50% - 35px);
  top: calc(50% - 10px);
  width: 80px;
  height: 40px;
  border-radius: 5px;
  font-weight: bold;
  font-size: 1rem;
  display: none;
  transition: all 1s;
  z-index: 10;
  background: #fff;
  cursor: pointer;
}

#projects .swiper-slide .item_img .item_bg {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 5;
}
#projects .swiper-slide .item_img .item_bg.denied{
  display: block;
  font-weight: bold;
  font-size: 2.5rem;
}
#projects .swiper-slide:hover .item_img button,
#projects .swiper-slide:hover .item_img .item_bg {
  display: block;
}

#projects .swiper-slide .item_skill_tag {
  margin: 8px 0;
}

#projects .swiper-slide .item_skill_tag ul {
  display: flex;
  flex-wrap: wrap;
  height: 30px;
}

#projects .swiper-slide .item_skill_tag ul li {
  font-size: 1rem;
  margin-right: 1rem;
  margin-bottom: 0.5rem;
  position: relative;
  line-height: 30px;
  height: 100%;
  padding: 0 4px;
  font-weight: bold;
  border: 2px solid rgba(230, 230, 230, 0.8);
}

/* ./components/menu/item/skills */
#skills {
  width: 100%;
  height: 100%;
}

#skills * {
  color: #222;
}

#skills .inner {
  max-width: 1300px;
  height: 75vh;
  background: #fff;
  margin-top: 5vh;
  position: relative;
  display: flex;
  border-radius: 50px;
}

/* 프로필 및 기준 */
#skills .left {
  width: 30%;
  height: 100%;
  border-right: 1px solid #222;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#skills .left img {
  width: 125px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 50px;
  object-fit: cover;
}

#skills .left>div {
  width: 275px;
  display: inline-block;
}

#skills .left ul:first-child {
  width: 35%;
  display: inline-block;
}

#skills .left ul:last-child {
  width: 65%;
  display: inline-block;
}

#skills .left ul:last-child li {
  border-left: 1px dotted #222;
  margin-left: 8px;
  text-align: center;
}

#skills .left li {
  text-align: right;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

/* skill 리스트 */
#skills .right {
  width: 70%;
  height: 100%;
  overflow: scroll;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

::-webkit-scrollbar {
  display: none;
}

#skills .right .skill_card {
  width: 40%;
  height: 150px;
  border: 1px solid grey;
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  margin-bottom: 50px;
}

#skills .right .skill_img {
  width: 25%;
  height: 100%;
  border-right: 1px solid #222;
  text-align: center;
}

#skills .right .skill_img img {
  width: 60px;
  height: 100%;
}

#skills .right .skill_star {
  width: 70%;
  height: 100%;
}

#skills .right .skill_star h2 {
  margin: 20px 0;
}

#skills .right .skill_star svg {
  width: 45px;
  height: 40px;
}

/* ./components/menu/Contact */
#contact {
  width: 100%;
  height: 85vh;
  display: flex;
  align-items: center;
}

#contact * {
  color: #222;
}

#contact .card_wrap {
  width: 600px;
  height: 600px;
  background: #fff;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

#contact .card_wrap .card {
  width: 95%;
  height: 95%;
  border: 2px dashed;
  position: relative;
}

#contact .card>div {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 70px;
}

#contact .card>div h1 {
  width: 300px;
  margin: 0 auto;
  border-bottom: 2px solid #222;
  margin-bottom: 50px;
  font-size: 3rem;
}

#contact .card h3 {
  height: 60px;
  margin-bottom: 1.5rem;
}

#contact .card>div li {
  cursor: pointer;
  margin-bottom: 5rem;
  font-size: 2rem;
}

#contact .card>div li svg {
  width: 75px;
  height: 75px;
}

/* ./components/Footer */
#footer {
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid rgba(100, 100, 100, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#footer>span {
  margin-left: 75px;
}

#footer ul {
  width: 100%;
  position: absolute;
  bottom: 0;
  transform: translateX(calc(50% - 15px));
}

#footer ul li {
  cursor: pointer;
  width: 200px;
}

#footer ul li svg {
  cursor: pointer;
  font-size: 2rem;
}

#footer p {
  margin-right: 125px;
}

#footer p span {
  animation: colorful 2s linear infinite;
}


/* media */
@media (max-width:499px) {

  /* header 로고 */
  #header span {
    left: 5%;
  }

  /* menu bars button */
  #header svg {
    display: block;
    position: absolute;
    right: 5%;
    top: calc(50% - 22.5px);
    width: 50px;
    height: 50px;
    cursor: pointer;
  }

  /* header 메뉴 */
  #header .menu {
    right: 0;
    top: 100%;
    width: 30%;
  }

  #header .menu li {
    float: none;
    width: 100%;
    text-align: right;
    padding: 0;
  }
}